export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '评价活动'
    }
  },
  data: function data() {
    return {
      content: null
    };
  },
  created: function created() {},
  beforeCreate: function beforeCreate() {
    this.form = this.$form.createForm(this, {
      name: 'form_in_modal'
    });
  },
  methods: {
    handleOk: function handleOk() {
      this.$emit('ok');
    },
    handleCancel: function handleCancel() {
      this.$emit('cancel');
    }
  }
};