import request from '@/utils/request';
export function getActivityAppraiseList(params) {
  return request({
    url: '/api/admin/activity/appraise',
    method: 'get',
    params: params
  });
}
export function appraiseActivity(id, params) {
  return request({
    url: "/api/admin/activity/appraise/".concat(id),
    method: 'put',
    params: params
  });
}