import * as Service from '@/api/activityAppraise';
import Pagination from '@/components/Pagination';
import AppraiseModal from "./components/AppraiseModal/index";
import FilterForm from "./components/FilterForm";
var columns = [{
  title: '活动名称',
  dataIndex: 'activity.name'
}, {
  title: '被评价用户',
  dataIndex: 'appraised_user.real_name'
}, {
  title: '评价人',
  key: 'user',
  dataIndex: 'user',
  scopedSlots: {
    customRender: 'user'
  }
}, {
  title: '评价内容',
  dataIndex: 'content',
  scopedSlots: {
    customRender: 'content'
  }
}, {
  title: '评价状态',
  dataIndex: 'is_done',
  scopedSlots: {
    customRender: 'isDone'
  }
}, {
  title: '评价时间',
  dataIndex: 'appraised_time',
  scopedSlots: {
    customRender: 'appraisedTime'
  }
}, {
  title: '操作',
  scopedSlots: {
    customRender: 'operation'
  }
}];
export default {
  components: {
    Pagination: Pagination,
    AppraiseModal: AppraiseModal,
    FilterForm: FilterForm
  },
  data: function data() {
    return {
      list: null,
      paginationData: {},
      columns: columns,
      loading: false,
      modalVisible: false,
      id: null
    };
  },
  computed: {},
  created: function created() {
    this.init();
  },
  methods: {
    init: function init(params) {
      var _this = this;

      this.loading = true;
      this.getList(params).then(function (status) {
        if (status) {
          _this.loading = false;
        }
      });
    },
    getList: function getList(params) {
      var self = this;
      return new Promise(function (resolve) {
        Service.getActivityAppraiseList(params).then(function (res) {
          self.list = res.data.data;
          self.paginationData = res.data;
          resolve(true);
        });
      });
    },
    handleAppraiseClick: function handleAppraiseClick(id) {
      this.modalVisible = true;
      this.id = id;
    },
    handleOk: function handleOk() {
      var _this2 = this;

      var form = this.$refs.appraiseModal.form;
      form.validateFields(function (err, params) {
        if (err) {
          return;
        }

        console.log('Received values of form: ', params);
        Service.appraiseActivity(_this2.id, params).then(function (res) {
          if (res.status) {
            _this2.$message.success('评价成功');
          }
        });

        _this2.resetForm();

        _this2.modalVisible = false;
      });
    },
    handleCancel: function handleCancel() {
      this.modalVisible = false;
      this.resetForm();
    },
    resetForm: function resetForm() {
      var form = this.$refs.appraiseModal.form;
      form.resetFields();
      this.id = null;
    },
    handleSearchClick: function handleSearchClick(params) {
      this.init(params);
    }
  }
};